.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
}

.form {
  display: flex;
  flex-direction: column;
  background-color: #545F7D;
  align-items: center;
  width: 400px;
  height: 500px;
  border-radius: 5px;
}

.buttonSubmit{
  color: #fff;
  margin-top: 20px;
  width: 100px;
  background-color: #44425E;
  border-radius: 5px;
  border: 1px solid;  
}